<template>
  <div class="login">
    手机号：<el-input v-model="inputPhone" placeholder="请输入手机号"></el-input>
    <img :src="imgSrc" @click="imgClick">
    图片验证码：<el-input v-model="imgNum" placeholder="图片验证码"></el-input>
    短信验证码：<el-input v-model="iphoneNum" placeholder="手机验证码"></el-input>
    <el-button @click="iphoneNumClick">获取手机验证码</el-button>
    <el-button type="primary" @click="login">登入</el-button>
  </div>
</template>

<script>
import Vue from 'vue'
import Axios from 'axios'
Axios.defaults.timeout = 5000
Axios.defaults.withCredentials = true
Axios.defaults.headers.post['Content-Type'] = 'application/json; charset=utf-8'
Vue.prototype.$ajax = Axios
export default {
  name: 'login',
  data () {
    return {
      inputPhone: '',
      imgNum: '',
      iphoneNum: '',
      imgSrc: '',
      contextPath: 'http://192.168.70.13:8083/mrs'
    }
  },
  created () {
    this.imgSrc = this.contextPath + '/basic/randomImg?' + Math.random()
  },
  methods: {
    imgClick () {
      this.imgSrc = this.contextPath + '/basic/randomImg?' + Math.random()
    },
    getNowFormatDate () {
      let date = new Date()
      let seperator1 = '-'
      let seperator2 = ':'
      let month = date.getMonth() + 1
      let strDate = date.getDate()
      if (month >= 1 && month <= 9) {
        month = '0' + month
      }
      if (strDate >= 0 && strDate <= 9) {
        strDate = '0' + strDate
      }
      let currentdate = date.getFullYear() + seperator1 + month + seperator1 + strDate + ' ' + date.getHours() + seperator2 + date.getMinutes() + seperator2 + date.getSeconds()
      return currentdate
    },
    login () {
      const loginOutUrl = this.contextPath + '/basic/login'
      this.$ajax.post(loginOutUrl, {
        appId: 'org_crm_merchant',
        loginPhone: this.inputPhone,
        reqTime: this.getNowFormatDate(),
        smscode: this.imgNum
      }).then((res) => {
        sessionStorage.clear()
        if (res.data.code === 0) {
          var arr = res.data.data.parentResources
          for (var i = 0; i < arr.length; i++) {
            switch (arr[i].uniqueCode) {
              case 'orderManage':
                sessionStorage.setItem('order', arr[i].id)
                break
              case 'lendingManage':
                sessionStorage.setItem('finance', arr[i].id)
                break
              case 'minimalistMode':
                sessionStorage.setItem('search', arr[i].id)
                break
              case 'confirmLoan':
                sessionStorage.setItem('loan', arr[i].id)
                break
              case 'immediateSuspen':
                sessionStorage.setItem('hang', arr[i].id)
                break
              case 'immediateCancel':
                sessionStorage.setItem('cancel', arr[i].id)
                break
            }
          }
          sessionStorage.setItem('phone', this.inputPhone)
          sessionStorage.setItem('token', res.data.data.token)
          sessionStorage.setItem('realName', res.data.data.realName)
          this.$router.push({ path: '/' })
        }
      })
    },
    iphoneNumClick () {
      const imgUrl = this.contextPath + '/basic/sendSms'
      this.$ajax.post(imgUrl, {
        loginPhone: this.inputPhone,
        picCode: this.imgNum
      }).then((res) => {
        console.log(res.data)
      })
    }
  },
  computed: {
  },
  components: {
  }
}
</script>
<style lang="scss" scoped>
  .login{
    img{
      width: 200px;
      height: 100px;
    }
  }
</style>



